
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Confirm,
Toast
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
adList: [],
carType: '1',
params: {
'phone': '',
'code': ''
},
search: {
page: 1,
pageSize: 15
},
params02:{
'sessionId':''
},
params03:{
'sessionId':''
},
params04:{
'sessionId':'',
'password':''
},
show5:false,
showcancel: false,
msg:'请输入密码',
placeholder:'请输入密码',
userMsg:{

},
login: true,
showPositionValue: false,
toast: '',
middle:'middle'
}
},
created () {
var carType = this.$route.query.carType || null
console.log('carType')
if(carType){
this.carType = carType
}
this.params04.sessionId = this.params03.sessionId = this.params02.sessionId = getCookie('sessionId')

this.getUserMsg()
this.judgePassword()
},
activated () {

},
methods: {
getUserMsg(){
const that = this;
Drive.prototype.getUserMsg(that.params02).then(res => {
let list = res.data.data || [];
that.userMsg = list
console.log(list)
});
},
judgePassword(){
const that = this;
Drive.prototype.judgePassword(that.params03).then(res => {
let list = res.data.data || [];
console.log(res.data.status)
if(res.data.status == 2000000){
that.login = true
}else {
that.login = false
}
});
},
toPassword(){
const that = this;
Drive.prototype.toPassword(that.params04).then(res => {
let list = res.data.data || {};
console.log(list)
if(res.data.status == 2000000){
that.login = true
}else {
that.login = false
that.toast = res.data.message
that.showPositionValue = true
}
});
},
onCancel () {
console.log('plugin cancel')
},
onConfirm (e) {
console.log(e)
this.params04.password = e
if(this.params04.password){
this.toPassword()
}else {
this.toast = '密码为空'
this.showPositionValue = true
}

},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
verifyCode () {
const that = this;
Drive.prototype.verify(that.params).then(res => {
let list = res.data.data || [];
console.log(list)
});
},
article () {
const that = this;
Drive.prototype.article(that.search).then(res => {
let list = res.data.data || [];
console.log(list)
});
},
onTypeTap (e) {
console.log(e)

this.carType = e
},
onSubjectOneTap (e){
console.log(e)
const that = this

if(e == 1){
that.$router.push('../skills?carType='+ this.carType + '&&type=' + '1')
}else {
that.$router.push('../confirmexam?carType='+ this.carType + '&&type=' + '1')
}





},
onSubjectTwoTap (e){
console.log(e)
const that = this

if(e == 1){
that.$router.push('../skills?carType='+ this.carType + '&&type=' + '2')
}else {
that.$router.push('../confirmexam?carType='+ this.carType + '&&type=' + '2')
}



},
onVideosTap(e){
const that = this
if(that.login){
that.$router.push('../videos?subjectsType='+ e)
}else {
this.show5 = true
}
},
onMsgTap(){

const that = this
that.$router.push('../detailmsg')
}
}
}



